
#login-frame1{
  z-index: 0;
  position: relative;


  display:inline-block;
  text-align: center;


  width: 26.8%;
  height: 30%;
  margin-left: 36%;
  margin-top: 5%;
}
#login-frame{

    position: relative;
    width: 100%;
    height: 100%;
    margin-top: -50%;

    display:grid;
   place-items: center;
   background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
    border-radius: 50px;
    z-index: -2;


}
#profile-img{
  margin-top: 2%;
}
#profile-circle{
  position: relative;
  fill: #ffffff;
  opacity: 0.9;
  margin-top: -100%;
}
#btn1 {
  position: relative;
  width: 40%;

  background: #ffffff;
  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
  border-radius: 20px;
  border-color: transparent;

  color: #7e7d7d;
  font-size: 1.25em;
  font-weight: 600;

  margin-top: 45%;
  padding:0.8em 2.8em;

  font-style: "Pretendard";
}

#btn2 {
  position: relative;
  width: 40%;


  background: #ffffff;
  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
  border-radius: 20px;
  border-color: transparent;

  color:  #7e7d7d;
  font-size: 1.25em;
  font-weight: 600;

  
  margin-top:8%;
  margin-bottom: 10%;

  padding:0.8em 2.8em;

  font-style: "Pretendard";
}

#btn3 {
  display: inline-block;
  position: relative;
  width: 40%;

  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;

  color: #8d8a8a;
  font-size: 1.25em;
  font-weight: 600;

  padding:0.8em 2.8em;
  font-style: "Pretendard";
}

#btn1:hover, #btn2:hover, #btn3:hover  {
  color: #ffffff;
  transition: color 300ms ease-in-out;
  background-color: #D8D7FB;
}


.inputField2~ #span2{
  font-family: "Pretendard";
  font-size: 1em;
  color: #3a3a3a;
  font-weight: 600;
  position: absolute;
  top: 79%;
  left: 20%;
  z-index: 1;
  transition: all ease-in-out 0.3s;
}
.inputField2:hover {
  border: 2px solid rgb(255, 255, 255);
}
.inputField2:focus-visible  {
  outline: none!important;
  border: 2px solid #8b81ff;
}
.inputField2:focus ~ #span2 {
  top : 71%;
  z-index: 1;
  font-size: 0.9em;
  transition: all ease-in-out 0.3s;
}

.inputField1~ #span1{
  font-family: "Pretendard";
  font-size: 1em;
  color: #3a3a3a;
  font-weight: 600;
  position: absolute;
  top: 54%;
  left: 22%;
  z-index: 1;
  transition: all ease-in-out 0.3s;
}
.inputField1:hover {
  border: 2px solid rgb(255, 255, 255);
}
.inputField1:focus-visible  {
  outline: none!important;
  border: 2px solid #8b81ff;
}
.inputField1:focus ~ #span1 {
  top : 46%;
  z-index: 1;
  font-size: 0.9em;
  transition: all ease-in-out 0.3s;
}
