


nav {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

#navbox1 {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;

    margin-top: 90%;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0.0625em 0px 2.5em rgba(112, 144, 176, 0.15);
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    box-sizing: border-box;
    padding: 3em 1.5em 3em 0.5em;
    transition: width 250ms;
}

#navbox {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    left: 0px;
    margin-top: 90%;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0.0625em 0px 2.5em rgba(112, 144, 176, 0.15);
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    box-sizing: border-box;
    padding: 3em 1.5em 3em 0.5em;
    transition: width 250ms;
}
#navbox1 #icon,
#navbox #icon
#as{
fill: #FFFFFF;
}

#navbox1:hover, #navbox:hover {
    width: 113%;
    background-color: transparent;    
}

#navbox1 #icon,
#navbox #icon,
#asidebox1 #icon,
#asidebox2 #icon,
#asidebox3 #icon {
    fill: #000000;
}

#navbox1:hover #icon,
#navbox:hover #icon,
#asidebox1:hover #icon,
#asidebox2:hover #icon,
#asidebox3:hover #icon {
    fill: #ffffff;
}

aside {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
}

#asidebox1,
#asidebox2,
#asidebox3 {
    position: fixed;
    display: flex;
    align-items: center;
    width: 4%;
    right: 0px;

    box-sizing: border-box;
    padding: 1.3em 0.5em 1.3em 1.5em;
    transition: width 250ms;
}

#asidebox1 {
    margin-top: 5.5%;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: -0.0625em 0px 2.5em rgba(112, 144, 176, 0.15);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}

#asidebox2 {
    margin-top: 12.5%;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: -0.0625em 0px 2.5em rgba(112, 144, 176, 0.15);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}

#asidebox3 {
    margin-top: 19.5%;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: -16px 0px 40px rgba(112, 144, 176, 0.15);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}

#asidebox1:hover,
#asidebox2:hover,
#asidebox3:hover {
    
    width: 4.5%;
    background-color: transparent;
}


#profilepic{
    width: 26%;
    height: 26%;
