#back{
    position: absolute;
    width: 100vw;
    height: 100vh;
  z-index: -1;
    background: 
    linear-gradient(#5041ff,transparent), 
    linear-gradient(-55deg,#c825f5,transparent), 
    linear-gradient(55deg,#4ffff4,transparent); 
  }

  

#introbox{
    position: relative;
    margin-left: 16.5%;
    margin-top: 4%;
}
#intro {
    font-family: 'Pretendard';
    font-weight: 700;
    color:  rgba(255, 255, 255, 0.90); 
    font-size: 8em;
    vertical-align: top;

}

